import {getGuides} from '@/api/mine.js'
export default {
    data(){
      return {
          guideList: []
      }
    },
    created(){
        this.getList()
    },
    methods:{
        getList(){
            getGuides().then(res => {
                console.log(res);
                this.guideList = res.data.guideUnitResponses
            })
        }
    }
}



